const regexLatLong = /[-]?[0-9]+[.][0-9]*$/;
const regexLokasiJabatan = /[0-9][0-9]+$/;

export function required(x: any) {
  return !!x || "Tidak boleh kosong";
}

export function latLong (s: string) {
  return regexLatLong.test(s) || "invalid latitude/longitude";
}

export function lokasiJabatan (s: string) {
  return regexLokasiJabatan.test(s) || "invalid lokasi jabatan";
}

export default {
  latLong,
  lokasiJabatan,
  required
}
