<template>
  <v-dialog width="500" persistent :value="true">
    <v-card>
      <v-toolbar flat color="grey lighten-4">
        <v-toolbar-title>Edit Data Lokasi #{{ this.id }}</v-toolbar-title>
      </v-toolbar>
      <v-card-text>
        <v-form ref="form" v-model="formValid">
          <v-text-field required v-model="item.nama" label="Nama" />
          <v-text-field required v-model="item.alamat" label="Alamat" />
          <v-select
            :items="options.jabatan"
            v-model="item.jabatan"
            label="Jabatan"
          />
          <v-text-field v-model="item.lokasiJabatan" :rules="[ rules.required, rules.lokasiJabatan ]" label="Lokasi Jabatan" />
          <v-text-field v-model="item.latitude" :rules="[ rules.required, rules.latLong ]" label="Latitude"/>
          <v-text-field v-model="item.longitude" :rules="[ rules.required, rules.latLong ]" label="Longitude"/>
        </v-form>
      </v-card-text>
      <v-card-actions>
        <v-btn outlined dark color="blue" @click="update">
          Simpan
        </v-btn>
        <v-btn outlined dark color="pink" @click="cancel">
          Batal
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import Vue from "vue";
import { node as defaultNode } from "@/services/defaults";
import { node as nodeRepo } from "@/services/repo";
import rules from "@/services/rules";
import evbus from "@/evbus";

export default Vue.extend({
  name: "DataAdd",
  props: ["id"],
  data: () => ({
    item: defaultNode(),
    options: {
      jabatan: ["rt", "rw"]
    },
    rules: {
      required: rules.required,
      latLong: rules.latLong,
      lokasiJabatan: rules.lokasiJabatan
    },
    formValid: false,
  }),
  methods: {
    loadData() {
      nodeRepo.findOne({ id: this.id })
        .then(item => {
          let { _id, ...rest } = item;
          this.item = rest;
        })
    },
    update() {
      if (!this.$refs.form.validate()) {
        alert("Periksa kembali data yang anda input!");
        return;
      };
      nodeRepo.update({ id: this.id, body: {...this.item} })
        .then(() => {
          evbus.$emit("admin-data-reload");
          this.$router.back();
        });
    },
    cancel() {
      this.$router.back();
    }
  },
  mounted() {
    console.log("this.id=", this.id);
    this.loadData();
  }
});
</script>

